  margin: 0 10px;
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.contact-radio {
  text-align: left;
}

.contact-sub button {
  background-color: #51707e;
  padding: 10px 30px;
  border: none;
  border-radius: 5px;
  /* color: white; */
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
  margin-left: 10px;
}

.checkmark {
  position: relative;
  top: 0;
  left: 0;
  height: 15px;
  width: 15px;
  background-color: #eee;
}

/* On mouse-over, add a grey background color */

.container-custom:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */

.container-custom input:checked ~ .checkmark {
  background-color: transparent;
}

/* Create the checkmark/indicator (hidden when not checked) */

.checkmark:after {
  content: "";
  position: relative;
  display: none;
}

/* Show the checkmark when checked */

.container-custom input:checked ~ .checkmark:after {
  display: block;
}

.container-custom {
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 80px;
  grid-gap: 8px;
  gap: 8px;
  margin-left: 5px;
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
  font-size: 0.1rem
}

.checkmark {
  width: 10px;
  height: 10px;
  background-color: transparent;
  border: 0.5px solid white;
}

/* Style the checkmark/indicator */

.container-custom .checkmark:after {
  left: 3px;
  top: 1.5px;
  width: 1px;
  height: 3px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(
45deg);
}

